  .header {
    background-image: image-set(url("../../img/bg-img/bg-header-desc@2x.webp") 2x, url("../../img/bg-img/bg-header-desc@2x.jpg") 2x);
  }
}
@media (max-width: 1023px) {
  .header {
    background-image: image-set(url("../../img/bg-img/bg-header-tabl.webp") 1x, url("../../img/bg-img/bg-header-tabl.jpg") 1x);
  }
}
@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
  .header {
    background-image: image-set(url("../../img/bg-img/bg-header-tabl@2x.webp") 2x, url("../../img/bg-img/bg-header-tabl@2x.jpg") 2x);
  }
}
@media (max-width: 767px) {
