.header__container {
  min-height: 650px;
}
@media (max-width: 1023px) {
  .header__container {
    min-height: 638px;
  }
}
@media (max-width: 767px) {
  .header__container {
    min-height: 500px;
  }
}

.header__wrapper {
  padding: 35px 0;
}
@media (max-width: 1023px) {
  .header__wrapper {
    padding: 28px 0;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    position: relative;
    padding: 20px 0;
  }
}

.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__burger {
  display: none;
}
@media (max-width: 767px) {
  .header__burger {
    padding: 0;
    display: block;
    width: 24px;
    height: 15px;
    border: none;
    background-color: transparent;
    background-image: url("../../img/svg/icon-burger-close.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    cursor: pointer;
  }
}
@media (max-width: 767px) and (hover: hover) {
  .header__burger:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
@media (max-width: 767px) {
  .header__burger.focus-visible:focus {
    opacity: 0.5;
  }
}

.header__content {
  padding-left: 7vw;
  padding-top: 155px;
}
@media (max-width: 1023px) {
  .header__content {
    padding-left: 0;
    padding-top: 185px;
  }
}
@media (max-width: 767px) {
  .header__content {
    padding-top: 110px;
  }
}

.header__wrapper.is-open .header__nav {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  width: 285px;
  min-height: 568px;
  display: grid;
  grid-template-columns: 1fr 35px;
  grid-template-rows: 25px 1fr;
  row-gap: 40px;
  background-color: #f9fbfd;
  padding-top: 20px;
  padding-left: 15px;
  align-items: flex-start;
}
.header__wrapper.is-open .header__logo svg {
  fill: #011c40;
}
.header__wrapper.is-open .header__burger {
  background-image: url("../../img/svg/icon-burger-open.svg");
  z-index: 10;
  margin-top: 3px;
}
.header__wrapper.is-open .header__site-menu {
  grid-column: 1/-1;
  grid-row: 2/3;
  display: flex;
  flex-direction: column;
  row-gap: 26px;
  align-items: start;
}
.header__wrapper.is-open .header__site-menu a {
  color: #011c40;
}
@media (hover: hover) {
  .header__wrapper.is-open .header__site-menu a:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
.header__wrapper.is-open .header__site-menu a.focus-visible:focus {
  opacity: 0.5;
}

.logo svg {
  fill: #f9fbfd;
  width: 70px;
  height: 38px;
}
@media (hover: hover) {
  .logo svg:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.logo svg.focus-visible:focus {
  opacity: 0.7;
}
.logo svg.focus-visible:active {
  opacity: 0.3;
}
@media (max-width: 1023px) {
  .logo svg {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo svg {
    width: 42px;
    height: 22px;
  }
}

.site-menu {
  list-style: none;
